@tailwind base;
@tailwind components;
@tailwind utilities;

.body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, Montserrat ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(circle, rgb(154, 106, 162) 0%, rgb(37, 0, 47) 70%);
  background-position: center;
  background-size: cover; /* Cover the entire element */
  background-repeat: no-repeat; /* Prevent repeating */
  height: 100vh; /* Example height */
  width: 100%; /* Example width */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.bg-clip-text {
  background-clip: text;
  -webkit-background-clip: text; /* For Safari */
  color: transparent; /* Hide the original text color */
}
.gradient-text-cyan {
  background-image: linear-gradient(to right, hsl(275, 63%, 59%), rgb(232, 204, 245)); /* Blue gradient colors */
  font-weight: 700;
  font-size: rem;
}
.gradient-text-purple{
  background-image: linear-gradient(to right, hsl(287, 100%, 50%), rgb(255, 255, 255)); /* Blue gradient colors */
  font-weight: 700;
  font-size: rem;

}
html {
  scroll-behavior: smooth;
}
/* For WebKit browsers (Chrome, Safari) */
.hide-scrollbars {
  overflow: hidden; /* Hide scrollbars */
  overflow-y: auto; /* Enable vertical scrolling */
}

/* WebKit browsers specific */
.hide-scrollbars::-webkit-scrollbar {
  display: none; /* Hide scrollbar */
}

/* For Firefox */
.hide-scrollbars {
  scrollbar-width: none; /* Hide scrollbar */
}

/* For IE and Edge */
.hide-scrollbars {
  -ms-overflow-style: none; /* Hide scrollbar */
}
.gradient-transition {
  position: relative;
  overflow: hidden;
}

.gradient-transition::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(to left, rgba(88, 69, 88, 0.347), rgba(172, 119, 172, 0.99));
  transition: .6s ease-in-out;
  border-radius: 20px;
  z-index: 0;
}

.gradient-transition:hover::before {
  transform: translate(100%);
}

.transition-transform {
  transition: transform 0.5s ease-in-out;
}
.transition-transform:hover{
  transform: scale(1.1);
}
/* trans */
.gradient-transitions {
  position: relative;
  overflow: hidden;
}

.gradient-transitions::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: -100%;
  width: 100%;
  height: 3px;
  background: linear-gradient(to right, rgba(141, 24, 141, 0.682), rgba(221, 16, 221, 0.99));
  transition: transform 0.3s ease-in-out;
  border-radius: 20px;
  z-index: 0;
}

.gradient-transitions:hover::before {
  transform: translateX(100%);
}

.gradient-transitions.active::before {
  transform: translateX(100%);
}


.about{
  background-image: url(../public/img/about.png);
  background-size: cover;
  height: auto;
  position: flex;
}
.about-body{
  background-color: #17041f;
}
.contact{
  background-image: url(../public/img/bgcontact.png);
  background-size: cover;
  height: auto;
  position: flex;
  
}
.wavy {
  animation-name: wavy;
  animation-duration: 1.06s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  position: relative;
}

@keyframes wavy {
  0% {
    top: 0px;
  }
  50% {
    top: -10px;
  }
  100% {
    top: 0px;
  }
}
.grayscale-img {
  filter: grayscale(100%);
  transition: filter 0.3s ease-in-out;
}

.grayscale-img:hover {
  filter: grayscale(0%);
}
